import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import { SnapshotOut } from 'mobx-state-tree'
import React, { useState } from 'react'
import { RouteComponentProps, useHistory } from 'react-router'
import { PageApp } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP } from '../../constants'
import { openPageNow, plata_fmt } from '../../helpers'
import { useStores } from '../../models/root-store/root-store-context'
import ProductoPedido from '../../models/types/ProductoPedido'
import { HojaRutaApi } from '../../services/api'
import './RutasTransportista.css'



const moment = require('moment-timezone')

interface RenderRutaProps {
  ruta: HojaRutaApi
}
const RenderRuta: React.FC<RenderRutaProps> = observer(({ ruta }) => {
  const [DropDetalles, setDropDetalles] = useState(false)
  const history = useHistory()
  const rootStore = useStores()
  const { transportistaStore } = rootStore

  return (
    <div>
      <div className="ruta-item-transportista pb-2">
        <img
          className="mt-2"
          style={{ height: 25, marginLeft: 10, marginRight: 10 }}
          src="assets/icon/ubicacion-gray.png"
        />
        <div>
          <div className="d-flex ">
            <div>
              <div className="col-12">
                <strong>Entrega #{ruta.id}</strong>
                <br />
                <span style={{ fontWeight: 'bolder' }}> Fecha:</span>{' '}
                <span>{moment(ruta.fecha).format('D [de] MMMM ')}</span>
                <br />
                {!DropDetalles && (
                  <a style={{ color: 'var(--rojo-abi)' }} onClick={() => setDropDetalles(!DropDetalles)}>
                    Ver detalles
                  </a>
                )}
                {DropDetalles && (
                  <a style={{ color: 'var(--rojo-abi)' }} onClick={() => setDropDetalles(!DropDetalles)}>
                    Ocultar detalles
                  </a>
                )}
              </div>
            </div>
          </div>
          <div>
            {DropDetalles && (
              <div
                className="col-12 d-flex flex-column"
                style={{ flex: 1, justifyContent: 'center', alignContent: 'center' }}
              >
                <div className="row">
                  <div className="col">
                    <div>
                      <strong>Punto de retiro</strong>
                    </div>
                    <div>
                      <span>{ruta.inicio.fabricante}</span>
                      <br />
                    </div>
                  </div>
                  <div className="col">
                    <div>
                      <strong>Punto de entrega</strong>
                    </div>
                    <div>
                      <span>{ruta.fin.direccion}</span>
                    </div>
                  </div>
                </div>
                <div>
                  <strong>Cantidad de articulos: </strong>{' '}
                  <span>
                    {ruta.pedido?.productos.reduce(
                      (previousValue: number, curr: SnapshotOut<typeof ProductoPedido>) =>
                        previousValue + curr.cantidad,
                      0,
                    )}
                  </span>
                </div>
                <div>
                  <>
                    <strong>Ganancia: </strong> <span>{plata_fmt(ruta.monto)}</span>
                  </>
                </div>
                <div
                  onClick={() => {
                    transportistaStore.setOportunidadDetalle(ruta)
                    history.push(RUTAS_APP.RUTA)
                  }}
                >
                  Mas Info
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
})

export const RutasTransportista: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const [Rutas, setRutas] = useState([] as HojaRutaApi[])
  const [Loading, setLoading] = useState(false)

  const setup = () => {
    setLoading(true)
    obtenerRutas()
  }

  React.useEffect(() => {
    setup()
  }, [])

  React.useEffect(() => {
    console.log('@rutas', Rutas)
  }, [Rutas])

  const obtenerRutas = () => {
    // @ts-ignore
    rootStore.environment.api.mis_rutas().then((result) => {
      if (result.kind === 'ok') {
        setRutas(result.data)
      }
      setLoading(false)
    })
  }

  const openRuta = (item: HojaRutaApi) => {
    return () => {
      transportistaStore.setOportunidadDetalle(item)
      openPageNow(RUTAS_APP.RUTA, history)
    }
  }

  return (
    <PageApp
      preset={"p-driver"}
      subBarraExternaHeader={<SubHeader links={[{ titulo: 'Historial de entregas' }]} />}
      sub_titulo={` `}
      mostrar_barra={false}
    >
      {Loading && (
        <div className="d-flex justify-content-center align-items-center" style={{ height: '100%' }}>
          <IonSpinner />
        </div>
      )}
      {!Loading && (
        <div id="rutas-transportista-content">
          <div className={'oportunidades-list-container py-2'}>
            <h3 className="text-center font-weight-bold my-3"> Historial de entregas</h3>
            {Loading && <IonSpinner name="crescent" className={'loadingSpinner'} />}

            {!Loading && Rutas.map((ruta, index) => <RenderRuta ruta={ruta} key={index} />)}
            {!Loading && Rutas.length === 0 && (
              <div className={'d-flex flex-column h-100 justify-content-center align-items-center'}>
                <span className={'text-center'}>No se encontraron entregas</span>
              </div>
            )}
          </div>
        </div>
      )}
    </PageApp>
  )
})

export default RutasTransportista
